import React from "react";

import "./card.css";

export default class Card extends React.Component {
  constructor(props) {
    super(props);
    this.data = {};
  }

  render() {
    var that = this;
    Object.assign(that.data, this.props);

    return (
      <div className="card">
        <h3 className="dh-h3">{that.data.title}</h3>
        <p>{that.data.content}</p>
      </div>
    );
  }
}



