import CommonModel from "./commonmodel.js";

export default class EventModel extends CommonModel {
  constructor(host) {
    super(host);
    this.data = {};
    this.host = host;
  }

  list(obj) {
    var that = this;
    var toGet = that.getData("", { path: "/event/list" });
    return toGet.then(function(res) {
      return res;
    });
  }
}



