import React from "react";
import DocumentTitle from "react-document-title";
import { Link } from "react-router-dom";

import PageIndex from "./../../widgets/pageindex/index.js";
import Grid from "./../../widgets/grid/index.js";
import Card from "./../../widgets/card/index.js";

import "./newspage.css";

export default class NewsPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
    this.data = {};
    this.gridOrder = {};
    this.gridSetting = {};
    this.gridSetting.tablet = {};
    this.gridSetting.tablet.height = 2;
    this.gridSetting.tablet.width = 4;
    this.gridSetting.tablet.cols = 12;

    this.gridSetting.mobile = {};
    this.gridSetting.mobile.height = 2;
    this.gridSetting.mobile.width = 6;
    this.gridSetting.mobile.cols = 6;
  }

  gridLayout(data, type) {
    var { height, width, cols } = this.gridSetting[type];
    var currentX = 0;
    var currentY = 0;
    var toReturn = [];
    if (data) {
      for (var a in data) {
        if (currentX === cols) {
          currentX = 0;
          currentY += height;
        }
        var obj = {};
        obj.i = a;
        obj.x = currentX;
        obj.y = currentY;
        obj.w = width;
        obj.h = height;
        obj.static = true;
        toReturn.push(obj);
        currentX += width;
      }
    }
    return toReturn;
  }

  gridOrderPrepare(data) {
    var that = this;
    var toReturn = {};
    if (data.length) {
      toReturn.layouts = {};
      toReturn.layouts.tablet = that.gridLayout(data, "tablet");
      toReturn.layouts.mobile = that.gridLayout(data, "mobile");
      toReturn.cols = {};
      toReturn.cols.tablet = that.gridSetting.tablet.cols;
      toReturn.cols.mobile = that.gridSetting.mobile.cols;
      toReturn.breakpoints = {};
      toReturn.breakpoints.tablet = 1023;
      toReturn.breakpoints.mobile = 767;
      toReturn.bannerArrays = [];
    }
    return toReturn;
  }

  fetchResult(obj) {
    var that = this;
    that.common.data.model.news.list(obj).then(function(data) {
      that.data.news = data;
      that.gridOrder = that.gridOrderPrepare(that.data.news);
      that.forceUpdate();
    });
  }

  componentDidMount() {
    var that = this;
    var toRequest = {};
    toRequest.from = 0;
    toRequest.count = 10;
    that.fetchResult(toRequest);
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var { layouts, breakpoints, cols } = that.gridOrder;

    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: "/" });
    pageindex_array.push({ label: lang.newspage_title, value: "/news" });

    return (
      <DocumentTitle title={`${lang.newspage_title}${lang.document_title}`}>
        <div className="newspage">
        <div className="dh-part">
          <PageIndex data={pageindex_array} />
        </div>
          
          <div>
            {layouts && breakpoints && cols ? (
              <Grid layouts={layouts} cols={cols} breakpoints={breakpoints}>
                {that.data.news !== undefined
                  ? that.data.news.map(function(v, i) {
                      return (
                        <div key={i} className="grid_items">
                          {v.url ? (
                            <Link to={v.url}>
                              <div className={`grid_items_inner`}>
                                <Card title={v.title} content={v.content} />
                              </div>
                            </Link>
                          ) : (
                            <div className={`grid_items_inner`}>
                              <Card title={v.title} content={v.content} />
                            </div>
                          )}
                        </div>
                      );
                    })
                  : ""}
              </Grid>
            ) : (
              ""
            )}
          </div>
        </div>
      </DocumentTitle>
    );
  }
}



