import React from "react";
import DocumentTitle from "react-document-title";
import PageIndex from "./../../widgets/pageindex/index.js";

import Calender from './../../widgets/calender/index.js';


import "./eventpage.css";

export default class EventPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
    this.data = {};
    this.data.events = [];
  }

  dateConvert(dateString){

  }

   componentDidMount() {
    var { common } = window;
    var that = this;
    common.data.model.event.list().then(function(res) {
      if(res.status){
        for(var a in res.data){
          var tmpObj = {};
          Object.assign(tmpObj, res.data[a]);
          tmpObj.start = new Date(res.data[a].start);
          tmpObj.end = new Date(res.data[a].end);
          that.data.events.push(tmpObj);
        }
        that.forceUpdate();
      }
    });
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: "/" });
    pageindex_array.push({ label: lang.eventpage_title, value: "/event" });
    return (
      <DocumentTitle title={`${lang.eventpage_title}${lang.document_title}`}>
        <div className="eventpage">
          <div className="dh-part">
            <PageIndex data={pageindex_array} />
          </div>
          <div>
            <div className="dh-part">
            <Calender value={that.data.events}/>
            </div>
          </div>
        </div>
      </DocumentTitle>
    );
  }
}



