import React from "react";
import BigCalendar from "react-big-calendar";
import moment from "moment";
import "react-big-calendar/lib/css/react-big-calendar.css";
import "./calender.css";



export default class Calender extends React.Component {
  constructor(props) {
    super(props);
    this.data = {};
  }

  selectedSlot(slotInfo) {
    var toPresent = '';
    // toPresent += `Title: ${slotInfo.confirm}`
    // toPresent += `\n`;
    // toPresent += `Start Time: ${slotInfo.start}`
    // toPresent += `\n`;
    // toPresent += `End Time: ${slotInfo.end}`
    alert(toPresent);
  }

  selectedEvent(event) {
    var toPresent = '';
    toPresent += `Title: ${event.title}`
    toPresent += `\n`;
    toPresent += `Start Time: ${event.start}`
    toPresent += `\n`;
    toPresent += `End Time: ${event.end}`
    alert(toPresent);
  }

  customeHeader(headerData) {
    return (
      <div>
        <div>{headerData.label}</div>
      </div>
    );
  }

 

  render() {
    var that = this;
    BigCalendar.setLocalizer(BigCalendar.momentLocalizer(moment));

    var messages = {};
    messages.today = "今日";
    messages.previous = "上一個";
    messages.next = "下一個";

    return (
      <div className="calender">
        <div className="calender-inner">
          <BigCalendar
            // selectable
            events={that.props.value?that.props.value:[]}
            views={["week"]}
            messages={messages}
            defaultView={BigCalendar.Views.WEEK}
            onSelectEvent={that.selectedEvent.bind(that)}
            // onSelectSlot={that.selectedSlot.bind(that)}
          />
        </div>
      </div>
    );
  }
}



